import { publishCourse } from 'app/js/course-manage/help';
import Intro from './intro';

publishCourse();

const intro = new Intro();
intro.init();
if(isTaskCreatePage()) {
  Intro.initTaskCreatePageIntro();
}
if(isInitTaskDetailIntro()) {
  Intro.initTaskDetailIntro();
}


//加载到

function isTaskCreatePage() {
    return $('#step-3').length;
}

function isInitTaskDetailIntro() {
  return !!$('.js-settings-list').lenght;
}



// 判断是否有任务有任务的时候直接展示第一个任务
if($('.js-settings-list').lenght > 0 ) {
  intro.initTaskDetailIntro('.js-settings-list');
}
